@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
.messageBox {
  background: #F3F3F3;
  border-radius: 5px;
  padding: 5px 20px;
  color: white;
  display: inline-block;
  max-width: 80%;
}

.messageText {
  width: 100%;
  letter-spacing: 0;
  float: left;
  font-size: 9pt;
  word-wrap: break-word;
  font-family: 'Noto Sans KR', sans-serif;
  margin: 0;
}

.messageText img {
  vertical-align: middle;
}

.messageContainer {
  display: flex;
  padding: 0 5%;
  margin-top: 3px;
  
}

.sentText {
  display: flex;
  align-items: center;
  font-family: Helvetica;
  color: #828282;
  letter-spacing: 0.3px;
  font-size: 13px;
}

.pl-10 {
  margin: 5px;
}

.pr-10 {
  padding-right: 10px;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
  
}

.colorWhite {
  color: white;
}

.colorDark {
  color: black;
}

.backgroundBlue {
  background: rgb(246, 143, 36) ;
  
}

.backgroundLight {
  background: #F3F3F3;
}

.messages {
  padding: 5% 0;
  overflow: auto;
  flex: auto;
}
.infoBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    border-radius: 4px 4px 0 0;
    height: 60px;
    width: 100%;
    border-bottom: solid 3px rgba(193, 193, 193, 0.5);

  }
  
  .leftInnerContainer {
    flex: 0.5 1;
    display: flex;
    align-items: center;
    margin-left: 5%;
    color: black;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 12.5pt;

  }
  
  .rightInnerContainer {
    display: flex;
    flex: 0.5 1;
    justify-content: flex-end;
    margin-right: 5%;
  }
  
  .onlineIcon {
    margin-right: 5%;
  }

  .flecha {
    width: 16px;
    height: 16px;
    margin-right: 10px;
  }
.form {
  display: flex;
  border-top: 2px solid #D3D3D3;
}

.input {
  border: none;
  border-radius: 0;
  padding: 2%;
  width: 80%;
  font-size: 1.2em;
}

input:focus, textarea:focus, select:focus{
  outline: none;
}

.sendButton {
  color: black !important;
  text-transform: uppercase;
  text-decoration: none;
  background: white;
  padding: 20px;
  display: inline-block;
  border: solid 0.5px rgba(193, 193, 193, 0.5);
  width: 20%;
  font-size: 7.5pt;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: bold;
}
.outerContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: rgb(255,205,44);
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(255,205,44,1)), to(rgba(249,165,25,1)));
    background: -webkit-linear-gradient(bottom, rgba(255,205,44,1) 0%, rgba(249,165,25,1) 100%);
    background: linear-gradient(0deg, rgba(255,205,44,1) 0%, rgba(249,165,25,1) 100%);
  }
  
  .container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    border-radius: 8px;
    height: 60%;
    width: 35%;
    
  }
  
  @media (min-width: 320px) and (max-width: 480px) {
    .outerContainer {
      height: 100%;
    }
  
    .container {
      width: 100%;
      height: 100%;
    }
  }
  
  @media (min-width: 480px) and (max-width: 1200px) {
    .container {
      width: 60%;
    }
  }
html, body {
    font-family: 'Roboto', sans-serif;
    padding: 0;
    margin: 0;
  }
  
  #root {
    height: 100vh;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .joinOuterContainer {
    display: flex;
    justify-content: center;
    text-align: center;
    height: 100vh;
    align-items: center;
    background: rgb(255,205,44);
background: -webkit-gradient(linear, left bottom, left top, from(rgba(255,205,44,1)), to(rgba(249,165,25,1)));
background: -webkit-linear-gradient(bottom, rgba(255,205,44,1) 0%, rgba(249,165,25,1) 100%);
background: linear-gradient(0deg, rgba(255,205,44,1) 0%, rgba(249,165,25,1) 100%);
  }
  
  .joinInnerContainer {
    width: 20%;
  }
  
  .joinInput {
    border-radius: 0;
    padding: 15px 20px;
    width: 100%;
  }
  
  .heading {
    color: white;
    font-size: 2.5em;
  }
  
  .button {
      color: #FFA34E !important;
      text-transform: uppercase;
      text-decoration: none;
      background: white;
      padding: 20px;
      border-radius: 5px;
      display: inline-block;
      border: none;
      width: 100%;
      font-weight: bold;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  
  .mt-20 {
    margin-top: 20px;
  }
  
  @media (min-width: 320px) and (max-width: 480px) {
    .joinOuterContainer {
      height: 100%;
    }
  
    .joinInnerContainer {
      width: 90%;
    }
  
  
  }
  
  button:focus {
    outline: 0;
  }

  .joinInnerContainer img {
    margin-bottom: 5%;

  }
